.main-articles {
  position: relative;
  background: #f4f4f4;
  padding-top: 50px;
  padding-bottom: 10px;
  @include response(1200px) {
    padding-top: 20px;
  }
  @include response(767px) {
    padding-top: 0;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -40px;
    @include response(1200px) {
      margin-left: -20px;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(33.333% - 40px);
    max-width: calc(33.333% - 40px);
    width: 100%;
    margin-left: 40px;
    margin-bottom: 40px;
    @include response(1200px) {
      flex: 0 0 calc(33.333% - 20px);
      max-width: calc(33.333% - 20px);
      margin-bottom: 20px;
      margin-left: 20px;
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 20px);
      max-width: calc(100% - 20px);
    }
    &--small {
      flex: 0 0 calc(25% - 26px);
      max-width: calc(25% - 26px);
      margin-left: 26px;
      @include response(1200px) {
        margin-left: 0;
        margin-right: 20px;
        flex: 0 0 calc(40% - 20px);
        max-width: calc(40% - 20px);
      }
      @include response(767px) {
        flex: 0 0 305px;
        max-width: 305px;
      }
    }
  }
  &__item-img {
    display: block;
    position: relative;
    background: #c4c4c4;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 20px;
    text-decoration: none;
    @include response(767px) {
      margin-left: -20px;
      margin-right: -20px;
      border-radius: 0;
      .post-list & {
        margin-left: 0;
        margin-right: 0;
        border-radius: 6px;
      }
    }
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-label {
    position: absolute;
    left: 12px;
    bottom: 12px;
    background: #e28b60;
    border-radius: 2px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: #fff;
    padding: 2px 4px;
    z-index: 1;
  }
  &__item-title {
    display: block;
    text-decoration: none;
    font-family: "Lora";
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
    color: #0b1014;
    margin-bottom: 12px;
    max-height: 72px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      font-size: 26px;
      line-height: 32px;
      max-height: 64px;
    }
    @include desktop-hover() {
      color: #e28b60;
    }
    &--small {
      font-size: 26px;
      line-height: 32px;
      max-height: 64px;
    }
  }
  &__item-descriptions {
    max-height: 72px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
}
