.editorial {
  position: relative;
  padding-top: 50px;
  padding-bottom: 60px;
  @include response(1200px) {
    padding-top: 20px;
  }
  &__box {
    max-width: 750px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__content {
    position: relative;
    margin-bottom: 30px;
  }
}

