.post {
  position: relative;
  padding-top: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: -46px;
  border-top: 1px solid #373737;
  @include response(1200px) {
    padding-top: 15px;
    display: block;
    margin-bottom: 0;
  }
  &__holder {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex: 0 0 1280px;
    max-width: 1280px;
    width: 100%;
    @include response(1300px) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__title {
    margin: 0 0 30px;
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    color: #0b1014;
    @include response(1200px) {
      font-size: 26px;
      line-height: 30px;
      margin-bottom: 20px;
    }
  }
  &__descriptions {
    max-width: 600px;
    margin-bottom: 20px;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  &__controls-left {
    padding-right: 20px;
  }
  &__author-main {
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    color: #e28b60;
    margin-bottom: 4px;
  }
  &__date {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #0b1014;
  }
  &__controls-right {
    flex: 0 0 auto;
  }
  &__content {
    position: relative;
    padding-bottom: 30px;
  }
  &__info {
    flex: 0 0 816px;
    max-width: 816px;
    padding-right: 64px;
    padding-bottom: 154px;
    position: relative;
    @include response(1200px) {
      max-width: 100%;
      padding-right: 0;
      padding-bottom: 50px;
    }
    &::before {
      content: "";
      position: absolute;
      top: 45px;
      bottom: 0;
      right: 0;
      width: 2px;
      background: #000;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__author {
    position: relative;
    display: flex;
    padding-top: 40px;
    border-top: 2px solid #f4f4f4;
    @include response(1200px) {
      padding-top: 30px;
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      flex-wrap: wrap;
      align-items: center;
    }
  }
  &__author-ava {
    position: relative;
    flex: 0 0 64px;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 20px;
    margin-top: 22px;
    @include response(1200px) {
      margin-top: 0;
      margin-right: 16px;
    }
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: .3s ease-in-out;
    }
  }
  &__author-box {
    position: relative;
  }
  &__author-head {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    @include response(1200px) {
      display: block;
      margin-bottom: 0;
    }
  }
  &__author-name {
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #0b1014;
    margin-right: 6px;
    text-decoration: none;
    @include desktop-hover() {
      text-decoration: underline;
    }
    @include response(1200px) {
      display: block;
      margin-bottom: 6px;
    }
  }
  &__author-total {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #aaa;
    flex: 0 0 auto;
  }
  &__author-text {
    margin-bottom: 12px;
    @include response(1200px) {
      flex: 0 0 100%;
      margin-top: 16px;
    }
  }
  &__author-btn {
    display: inline-flex;
    vertical-align: top;
    align-items: center;
    text-decoration: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #aaa;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      color: #000;
      svg {
        fill: #000;
      }
    }
    svg {
      flex: 0 0 auto;
      fill: #aaa;
      margin-left: 4px;
      transition: 0.3s ease-in-out;
    }
  }
  &__aside {
    position: sticky;
    top: 84px;
    flex: 0 0 400px;
    max-width: 400px;
    width: 400px;
    padding-bottom: 60px;
    @include response(1200px) {
      display: none;
    }
  }
  &__aside-link {
    display: block;
    text-decoration: none;
    border-radius: 5px;
    overflow: hidden;
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      transition: 0.3s ease-in-out;
    }
  }
}

.post-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -26px;
  @include response(1200px) {
    margin-left: -20px;
    margin-right: -20px;
    overflow-x: auto;
    padding-left: 20px;
    padding-right: 20px;
    flex-wrap: nowrap;
  }
}
