.text-content {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  color: #0b1014;
  font-weight: 400;
  p {
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  ol {
    list-style: none;
    padding: 0;
    counter-reset: myCounter;
    margin: 10px 0 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 14px;
      position: relative;
      padding-left: 35px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) " .";
        position: absolute;
        left: 0;
        top: 0;
        width: 35px;
        text-align: center;
        color: #e28b60;
        font-weight: 700;
      }
    }
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 10px;
      position: relative;
      padding-left: 35px;
      &::before {
        content: "";
        position: absolute;
        left: 8px;
        top: 9px;
        width: 7px;
        height: 7px;
        background: #e28b60;
        border-radius: 50%;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 0 0 30px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    font-family: "Lora";
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    margin: 0 0 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    @include response(767px) {
      font-size: 26px;
      line-height: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    font-family: "Lora";
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    margin: 0 0 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    @include response(767px) {
      font-size: 26px;
      line-height: 32px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    font-family: "Lora";
    font-weight: 700;
    font-size: 26px;
    line-height: 30px;
    margin: 0 0 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    @include response(767px) {
      font-size: 20px;
      line-height: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin: 0 0 20px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  > img {
    display: inline-block;
    vertical-align: top;
    margin: 0 0 20px;
    border-radius: 5px;
    @include response(767px) {
      margin-left: -20px;
      margin-right: -20px;
      max-width: calc(100% + 40px);
      width: calc(100% + 40px);
      border-radius: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    text-decoration-line: underline;
    color: #aaa;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  small {
    font-size: 12px;
    line-height: 18px;
  }
  table {
    border-collapse: collapse;
    width: 100%;
    background: #f4f4f4;
    thead {
      td {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        background: #0b1014;
        padding: 6px 16px;
        @include response(767px) {
          padding: 5px 10px;
        }
        &:first-child {
          border-radius: 5px 0 0 0;
        }
        &:last-child {
          border-radius: 0 5px 0 0;
        }
      }
    }
    tr {
      border-bottom: 1px solid #fff;
      &:last-child {
        border-bottom: 0;
      }
    }
    td {
      border-right: 1px solid #fff;
      &:first-child {
        min-width: 165px;
      }
      &:last-child {
        border-right: 0;
      }
    }
    tbody {
      td {
        padding: 6px 16px;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #0b1014;
        @include response(767px) {
          padding: 5px 10px;
        }
      }
    }
  }
  blockquote {
    display: block;
    position: relative;
    background: rgba(247, 211, 159, 0.2);
    border-radius: 5px;
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 24px;
    color: #0b1014;
    font-style: italic;
    margin: 0 0 30px;
    padding: 20px;
    @include response(1200px) {
      margin-bottom: 20px;
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      border-radius: 0;
      align-items: flex-start;
      font-size: 14px;
      line-height: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    img {
      margin: 0 16px 0 0 !important;
    }
  }
  .imgs-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -30px;
    @include response(1200px) {
      margin-left: -10px;
    }
    &__item {
      flex: 0 0 calc(33.333% - 30px);
      max-width: calc(33.333% - 30px);
      width: 100%;
      margin-left: 30px;
      margin-bottom: 30px;
      @include response(1200px) {
        margin-bottom: 20px;
        flex: 0 0 calc(33.333% - 10px);
        max-width: calc(33.333% - 10px);
        margin-left: 10px;
      }
    }
  }
}
