body {
  height: 100%;
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  font-family: "Lato", "Lora", sans-serif;
  color: #0b1014;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  @include response(1200px) {
    ::-webkit-scrollbar {
      display: none;
    }
  }
}

.container {
  position: relative;
  max-width: 1280px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1300px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.wrapper {
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: hidden;
  flex: 1 1 auto;
  padding-top: 72px;
  &--overflow-inherit {
    overflow: inherit;
  }
  @include response(1200px) {
    padding-top: 56px;
  }
}

* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
