.social {
  position: relative;
  list-style: none;
  margin: 0 0 0 20px;
  flex: 0 0 auto;
  padding: 0;
  display: flex;
  li {
    flex: 0 0 auto;
    margin-right: 4px;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: flex;
    width: 24px;
    height: 24px;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      transform: translateY(-2px);
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
