.footer {
  position: relative;
  flex: 0 0 auto;
  background: #0b1014;
  padding-top: 120px;
  padding-bottom: 60px;
  @include response(1200px) {
    padding-top: 40px;
    padding-bottom: 50px;
    text-align: center;
  }
  &__container {
    display: flex;
    @include response(1200px) {
      flex-direction: column-reverse;
    }
  }
  &__left {
    flex: 0 0 auto;
    margin-right: 200px;
    max-width: 270px;
    @include response(1200px) {
      margin-right: 0;
      max-width: 100%;
    }
  }
  &__logo {
    position: relative;
    margin-bottom: 30px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__info {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    @include response(1200px) {
      justify-content: center;
    }
    li {
      flex: 0 0 auto;
      margin-bottom: 8px;
      margin-right: 20px;
      position: relative;
      &:last-child {
        margin-right: 0;
        &::before {
          display: none;
        }
      }
      &::before {
        content: "";
        position: absolute;
        width: 2px;
        height: 2px;
        background: #fff;
        border-radius: 50%;
        top: 50%;
        margin-top: -1px;
        right: -11px;
      }
    }
    a {
      display: block;
      text-decoration: none;
      color: #fff;
      font-size: 14px;
      line-height: 175%;
      @include response(1200px) {
        font-size: 12px;
      }
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
  &__copy {
    font-size: 14px;
    line-height: 17px;
    color: #fff;
    opacity: 0.4;
    @include response(1200px) {
      font-size: 12px;
    }
  }
  &__right {
    position: relative;
    display: flex;
    @include response(1200px) {
      margin-bottom: 50px;
      display: block;
    }
  }
  &__cell {
    flex: 0 0 auto;
    max-width: 200px;
    margin-right: 80px;
    @include response(1200px) {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 40px;
    }
    &:last-child {
      margin-right: 0;
      @include response(1200px) {
        margin-bottom: 0;
      }
    }
  }
  &__cell-title {
    position: relative;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    color: #fff;
    margin-bottom: 28px;
    padding-bottom: 18px;
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 40px;
      height: 4px;
      background: #f7d39f;
      @include response(1200px) {
        left: 50%;
        margin-left: -20px;
      }
    }
  }
  &__cell-list {
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;
    li {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
      color: #fff;
      font-size: 16px;
      line-height: 24px;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
}
