.error-page {
  position: relative;
  padding-top: 68px;
  flex: 1 1 auto;
  @include response(1200px) {
    padding-top: 80px;
    padding-bottom: 60px;
  }
  &__img {
    width: 100%;
    @include response(1200px) {
      height: auto;
      transform: none;
      bottom: auto;
      width: auto;
      margin-left: -20px;
      max-width: calc(100% + 40px);
      margin-right: -20px;
    }
  }
  &__box {
    position: relative;
    max-width: 750px;
    width: 100%;
    margin-left: auto;
    margin-right: 40px;
    margin-top: -240px;
    padding-bottom: 100px;
    @include response(1440px) {
      max-width: 650px;
      padding-bottom: 30px;
      margin-top: -200px;
    }
    @include response(1200px) {
      max-width: 100%;
      margin-right: 0;
      text-align: right;
      margin-top: -80px;
      padding-bottom: 0;
    }
  }
  &__title {
    margin-bottom: 2px;
    font-family: "Lora";
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    color: #e28b60;
    @include response(1200px) {
      font-size: 26px;
      line-height: 30px;
      padding-right: 16px;
    }
  }
  &__text {
    margin-bottom: 20px;
    @include response(1200px) {
      margin-right: 16px;
      max-width: 187px;
      display: inline-flex;
      margin-bottom: 30px;
    }
  }
  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    @include response(767px) {
      display: block;
    }
  }
  &__btn {
    flex: 0 0 auto;
    margin-right: 20px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 12px;
      width: 100%;
    }
  }
  &__form {
    position: relative;
    flex: 1 1 auto;
    display: flex;
  }
  &__input {
    flex: 1 1 auto;
    border: 1px solid #0b1014;
    border-right: 0;
    border-radius: 4px 0 0 4px;
    padding-left: 16px;
    padding-right: 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #0b1014;
    height: 48px;
    &::placeholder {
      color: #0b1014;
    }
  }
  &__form-btn {
    flex: 0 0 52px;
    width: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
    padding: 0;
    cursor: pointer;
    border-radius: 0 4px 4px 0;
    background: #0b1014;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #e28b60;
    }
    svg {
      fill: #fff;
    }
  }
}
