.team {
  position: relative;
  &__title {
    font-family: "Lora";
    font-weight: 700;
    font-size: 26px;
    line-height: 30px;
    color: #0b1014;
    margin-bottom: 30px;
    @include response(767px) {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 20px;
    }
  }
  &__list {
    position: relative;
    @include response(767px) {
      margin-left: -20px;
      margin-right: -20px;
    }
  }
  &__item {
    position: relative;
    padding-top: 40px;
    padding-bottom: 30px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f4f4f4;
    @include response(767px) {
      border-width: 2px;
      padding: 16px 20px;
      align-items: flex-start;
    }
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
      border-bottom: 0;
    }
  }
  &__ava {
    position: relative;
    flex: 0 0 140px;
    width: 140px;
    height: 140px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 20px;
    @include response(767px) {
      flex: 0 0 64px;
      width: 64px;
      height: 64px;
      margin-right: 16px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
  }
  &__item-top {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }
  &__item-name {
    font-weight: 900;
    font-size: 18px;
    line-height: 24px;
    color: #0b1014;
    @include response(767px) {
      line-height: 22px;
    }
  }
  &__item-info {
    position: relative;
  }
}
