.box {
  position: relative;
  padding-top: 50px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  @include response(1200px) {
    padding-top: 40px;
    display: block;
  }
  &__holder {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex: 0 0 1280px;
    max-width: 1280px;
    width: 100%;
    @include response(1300px) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__content {
    position: relative;
    flex: 0 0 840px;
    max-width: 840px;
    width: 100%;
    padding-right: 40px;
    padding-bottom: 60px;
    @include response(1200px) {
      max-width: 100%;
      padding-right: 0;
    }
    &::before {
      content: "";
      position: absolute;
      top: 45px;
      bottom: 0;
      right: 0;
      width: 2px;
      background: #000;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__aside {
    position: relative;
    flex: 0 0 400px;
    max-width: 400px;
    width: 100%;
    padding-bottom: 60px;
    @include response(1200px) {
      display: none;
    }
  }
  &__top-btn {
    position: sticky;
    width: 54px;
    top: 80vh;
    margin-right: -160px;
    margin-left: 106px;
    margin-bottom: 40px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    cursor: pointer;
    color: #0b1014;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: .2s ease-in-out;
    opacity: 0;
    visibility: hidden;
    &.scroll:not(.scroll-down) {
      opacity: 1;
      visibility: visible;
    }
    @include response(1620px) {
      margin-left: 26px;
      margin-right: -80px;
    }
    @include response(1430px) {
      display: none;
    }
    @include desktop-hover() {
      .box__top-btn-icon {
        transform: translateY(-3px);
      }
    }
  }
  &__top-btn-icon {
    width: 40px;
    height: 40px;
    transition: 0.3s ease-in-out;
    border: 2px solid #0b1014;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 4px;
  }
}
