.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: #f7d39f;
  border-bottom: 4px solid #373737;
  @include response(1200px) {
    border-bottom: 0;
  }
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left {
    position: relative;
    flex: 0 0 auto;
  }
  &__logo {
    position: relative;
    @include response(1200px) {
      width: 129px;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__center {
    position: relative;
    flex: 0 0 auto;
  }
  &__nav {
    position: relative;
    @include response(1200px) {
      order: 2;
      margin-bottom: 113px;
    }
  }
  &__nav-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    @include response(1200px) {
      display: block;
      border-top: 2px solid #000;
      border-bottom: 2px solid #000;
    }
    > li {
      position: relative;
      flex: 0 0 auto;
      @include response(1200px) {
        border-bottom: 2px solid #000;
        &:last-child {
          border-bottom: 0;
        }
      }
      @include desktop-hover() {
        > ul {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
        }
      }
      > a {
        display: block;
        text-decoration: none;
        font-family: "Lora";
        font-weight: 600;
        font-size: 18px;
        line-height: 24px;
        color: #0b1014;
        padding: 12px 23px;
        position: relative;
        @include response(1200px) {
          padding: 16px 20px 16px 0;
        }
        @include desktop-hover() {
          &::before {
            transform: scaleX(1);
          }
        }
        &::before {
          content: "";
          position: absolute;
          bottom: 12px;
          left: 23px;
          right: 23px;
          height: 2px;
          background: #0b1014;
          transform-origin: 0 50%;
          transform: scaleX(0);
          transition: 0.3s ease-in-out;
        }
      }
      > ul {
        list-style: none;
        margin: 0;
        padding: 12px 0;
        position: absolute;
        top: 100%;
        left: 0;
        width: 250px;
        background: #fff;
        box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.1);
        border-radius: 6px;
        transform: translateY(-10px);
        opacity: 0;
        visibility: hidden;
        transition: 0.3s ease-in-out;
        @include response(1200px) {
          opacity: 1;
          transform: none;
          visibility: visible;
          box-shadow: none;
          position: relative;
          top: auto;
          width: 100%;
          background: transparent;
          padding-top: 0;
          display: none;
        }
        li {
          position: relative;
          @include response(1200px) {
            border-bottom: 1px solid #000;
            &:last-child {
              border-bottom: 0;
            }
          }
        }
        a {
          position: relative;
          display: inline-block;
          vertical-align: top;
          text-decoration: none;
          font-family: "Lora";
          font-weight: 600;
          font-size: 14px;
          line-height: 20px;
          color: #0b1014;
          padding: 10px 23px;
          @include response(1200px) {
            display: block;
          }
          @include desktop-hover() {
            &::before {
              transform: scaleX(1);
            }
          }
          &::before {
            content: "";
            position: absolute;
            bottom: 10px;
            left: 23px;
            right: 23px;
            height: 2px;
            background: #0b1014;
            transform-origin: 0 50%;
            transform: scaleX(0);
            transition: 0.3s ease-in-out;
          }
        }
      }
    }
  }
  &__right {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    @include response(1200px) {
      position: initial;
    }
  }
  &__search {
    position: relative;
    @include response(1200px) {
      position: initial;
    }
  }
  &__search-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 30px;
    height: 30px;
    background: #0b1014;
    border-radius: 2px;
    transition: 0.3s ease-in-out;
    z-index: 1;
    @include desktop-hover() {
      background: #fff;
      svg {
        fill: #0b1014;
      }
    }
    .active-search & {
      background: #fff;
      svg {
        fill: #000;
      }
    }
    svg {
      fill: #f7d39f;
      transition: 0.3s ease-in-out;
    }
  }
  &__search-btn-open {
    .active-search & {
      display: none;
    }
  }
  &__search-btn-close {
    display: none;
    .active-search & {
      display: block;
    }
  }
  &__search-box {
    position: absolute;
    right: 0;
    top: 60px;
    z-index: 1;
    display: flex;
    width: 1280px;
    border-radius: 4px;
    overflow: hidden;
    transform: translateY(-40px);
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    .active-search & {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
      @include response(1200px) {
        transform: translateY(0);
      }
    }
    @include response(1200px) {
      width: calc(100% - 40px);
      top: 100%;
      display: block;
      right: 20px;
      transform: translateX(0);
      transform: translateY(-100%);
      overflow: visible;
    }
  }
  &__search-input {
    flex: 1 1 auto;
    height: 58px;
    font-size: 16px;
    color: #0b1014;
    padding-left: 16px;
    padding-right: 16px;
    border: 0;
    border-radius: 0;
    &::placeholder {
      color: #0b1014;
    }
    @include response(1200px) {
      width: 100%;
      height: 46px;
      margin-bottom: 10px;
      box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    }
  }
  &__search-button {
    position: relative;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: #e28b60;
    flex: 0 0 auto;
    border: 0;
    color: #fff;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: 100%;
      height: 46px;
      margin-bottom: 10px;
    }
    @include desktop-hover() {
      background: #0b1014;
    }
  }
  &__menu-btn {
    display: none;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      background: #0b1014;
      border-radius: 2px;
      flex: 0 0 30px;
      margin-left: 16px;
    }
  }
  &__mobile {
    display: none;
    @include response(1200px) {
      display: block;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 100;
      transform: translateX(100%);
      opacity: 0;
      visibility: hidden;
      &.active {
        transform: none;
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__mobile-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#000, 0.7);
    opacity: 0;
    transition: 0.3s ease-in-out;
    .header__mobile.active & {
      opacity: 1;
    }
  }
  &__mobile-box {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    max-width: 375px;
    width: 100%;
    background: #f7d39f;
    padding: 18vh 35px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: auto;
    transform: translateX(100%);
    transition: 0.3s ease-in-out;
    .header__mobile.active & {
      transform: translateX(0);
    }
  }
  &__mobile-close {
    position: absolute;
    top: 65px;
    right: 20px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #0b1014;
    border-radius: 2px;
  }
  &__mobile-title {
    margin-bottom: 50px;
    font-weight: bold;
    font-size: 36px;
    line-height: 46px;
    color: #0b1014;
    order: 1;
    font-family: "Lora";
  }
  &__mobile-logo {
    order: 3;
  }
  &__overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#000, 0.6);
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    transition: 0.3s ease-in-out;
    .active-search & {
      opacity: 1;
      visibility: visible;
      overflow: visible;
    }
  }
}

.menu-item-has-children {
  @include desktop-hover() {
    > a {
      &::after {
        transform: scaleY(-1);
      }
    }
  }
  > a {
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      margin-top: -2px;
      right: 12px;
      width: 0;
      height: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 6px solid #0b1014;
    }
  }
  @include response(1200px) {
    &.active {
      > a {
        &::after {
          transform: scaleY(-1);
        }
      }
      > ul {
        display: block;
      }
    }
  }
}
