.aside-item {
  position: relative;
  margin-bottom: 40px;
  display: flex;
  &--revers {
    flex-direction: row-reverse;
    margin-top: 60px;
    margin-bottom: 60px;
  }
  &--big {
    display: block;
    margin-top: 80px;
    margin-bottom: 80px;
  }
  &:last-child {
    margin-bottom: 0;
  }
  &__img {
    flex: 0 0 140px;
    max-width: 140px;
    width: 100%;
    height: 100px;
    background: #c4c4c4;
    border-radius: 3px;
    overflow: hidden;
    margin-right: 16px;
    text-decoration: none;
    .aside-item--revers & {
      flex: 0 0 112px;
      max-width: 112px;
      height: 112px;
      margin-left: 20px;
      margin-right: 0;
    }
    .aside-item--big & {
      display: block;
      max-width: 100%;
      height: auto;
      margin-right: 0;
      margin-bottom: 20px;
      border-radius: 6px;
    }
    @include desktop-hover() {
      img {
        transform: scale(1.05);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
      border-radius: 3px;
    }
  }
  &__box {
    position: relative;
  }
  &__label {
    margin-bottom: 8px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: #e28b60;
  }
  &__title {
    display: block;
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    max-height: 72px;
    overflow: hidden;
    color: #0b1014;
    transition: 0.3s ease-in-out;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    @include desktop-hover() {
      color: #e28b60;
    }
    .aside-item--revers & {
      font-size: 26px;
      line-height: 30px;
      max-height: 120px;
      -webkit-line-clamp: 4;
    }
    .aside-item--big & {
      font-size: 26px;
      line-height: 30px;
      max-height: 120px;
    }
  }
}

.aside-list {
  position: relative;
}
