.title {
  position: relative;
  padding-bottom: 20px;
  border-bottom: 2px solid #000;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #0b1014;
  text-transform: uppercase;
  margin-bottom: 40px;
  @include response(1200px) {
    margin-bottom: 30px;
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 12px;
  }
}
