.share {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  li {
    flex: 0 0 auto;
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
  }
  button {
    display: flex;
    padding: 0;
    border: 0;
    cursor: pointer;
    background: transparent;
    border-radius: 50%;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      transform: translateY(-2px);
    }
  }
}
