.category {
  position: relative;
  padding-top: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  @include response(1200px) {
    padding-top: 30px;
    display: block;
  }
  &__title {
    position: relative;
  }
  &__container {
    flex: 0 0 1280px;
    max-width: 1280px;
    width: 100%;
    @include response(1300px) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__row {
    position: relative;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
