.btn {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  transition: 0.3s ease-in-out;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #0b1014;
  padding: 13px 24px;
  border: 1px solid #0b1014;
  border-radius: 5px;
  @include desktop-hover() {
    background: #0b1014;
    color: #fff;
  }
}

.btn-2 {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  transition: 0.3s ease-in-out;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  background: #e28b60;
  border-radius: 5px;
  padding: 14px 25px;
  border: 0;
  @include desktop-hover() {
    background: #0b1014;
    color: #fff;
  }
  &--big {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
