.anchor-box {
  position: relative;
  margin-bottom: 40px;
  &__header {
    position: relative;
    display: flex;
  }
  &__item {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    padding: 10px 16px;
    color: #0b1014 !important;
    text-decoration: none !important;
    background: #f4f4f4;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #f7d39f;
    }
    &:first-child {
      border-radius: 5px 0 0 0;
    }
    &:last-child {
      border-radius: 0 5px 0 0;
    }
    &.active {
      background: #f7d39f;
    }
    img {
      flex: 0 0 auto;
      margin: 0 4px 0 0 !important;
    }
  }
  &__body {
    border: 8px solid #f7d39f;
    border-radius: 0px 5px 5px 5px;
    padding: 22px;
    display: flex;
    @include response(767px) {
      padding: 12px 16px;
      border-width: 4px;
      display: block;
    }
  }
  &__left {
    position: relative;
    display: flex;
    align-items: center;
    @include response(767px) {
      flex-direction: row-reverse;
      align-items: flex-start;
      margin-bottom: 14px;
    }
  }
  &__img {
    flex: 0 0 140px;
    width: 140px;
    margin-right: 20px;
    border-radius: 5px;
    overflow: hidden;
    @include response(767px) {
      margin-right: 0;
      margin-left: 12px;
      flex: 0 0 100px;
      width: 100px;
    }
    img {
      width: 100%;
    }
  }
  &__content {
    position: relative;
    @include response(767px) {
      flex: 1 1 auto;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #0b1014;
    margin-bottom: 16px;
    margin-right: -10px;
    @include response(767px) {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 6px;
      margin-right: 0;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #0b1014;
    padding-right: 20px;
    @include response(767px) {
      font-size: 14px;
      line-height: 18px;
      padding-right: 0;
    }
  }
  &__right {
    position: relative;
    flex: 0 0 200px;
    max-width: 200px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    @include response(767px) {
      max-width: 100%;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__logo {
    margin-bottom: 16px;
    padding-left: 20px;
    @include response(767px) {
      padding-left: 0;
      margin-right: 20px;
      margin-bottom: 0;
      flex: 0 0 70px;
      max-width: 70px;
      width: 100%;
    }
  }
  &__btn {
    width: 100%;
    &.btn-2 {
      color: #fff;
      text-decoration: none;
      padding-top: 17px;
      padding-bottom: 17px;
      font-size: 16px;
      line-height: 20px;
      @include response(767px) {
        padding-top: 8px;
        padding-bottom: 8px;
      }
    }
  }
}
