.title-section {
  position: relative;
  margin: 0 0 30px;
  padding-left: 26px;
  padding-top: 8px;
  padding-bottom: 10px;
  @include response(1200px) {
    padding-top: 4px;
    padding-bottom: 2px;
    padding-left: 16px;
  }
  &::before {
    content: "";
    position: absolute;
    width: 10px;
    top: 0;
    left: 0;
    bottom: 0;
    background: #e28b60;
    border-radius: 4px;
    @include response(1200px) {
      width: 6px;
    }
  }
  &__head {
    font-weight: 700;
    font-size: 26px;
    line-height: 30px;
    color: #e28b60;
    margin: 0 0 6px;
    font-family: "Lora";
    @include response(1200px) {
      font-size: 20px;
      line-height: 24px;
    }
  }
  &__text {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #0b1014;
    @include response(1200px) {
      font-size: 16px;
      line-height: 20px;
    }
  }
}
