.contacts {
  position: relative;
  padding-top: 60px;
  padding-bottom: 60px;
  @include response(1200px) {
    padding-top: 30px;
  }
  &__decor {
    position: absolute;
    bottom: 0;
    right: 0;
    @include response(1200px) {
      display: none;
    }
  }
  &__form {
    position: relative;
    max-width: 500px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &__form-row {
    margin-bottom: 20px;
    position: relative;
  }
  &__form-label {
    margin-bottom: 6px;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    color: #0b1014;
  }
  &__input-box {
    position: relative;
    input {
      width: 100%;
      border: 1px solid #aaa;
      border-radius: 4px;
      height: 46px;
      font-weight: 400;
      font-size: 16px;
      padding-left: 16px;
      padding-right: 16px;
      transition: 0.3s ease-in-out;
      color: #000;
      &::placeholder {
        color: #aaa;
      }
      &:focus {
        border-color: #e28b60;
      }
    }
    textarea {
      width: 100%;
      border: 1px solid #aaa;
      border-radius: 4px;
      height: 155px;
      resize: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      padding: 11px 16px;
      transition: 0.3s ease-in-out;
      color: #000;
      &::placeholder {
        color: #aaa;
      }
      &:focus {
        border-color: #e28b60;
      }
    }
  }
  &__form-recaptcha {
    position: relative;
    margin-top: 30px;
    margin-bottom: 20px;
  }
  &__form-btn-box {
    position: relative;
  }
  &__form-btn {
    width: 100%;
  }
}
