.btn-top {
  position: absolute;
  right: 100px;
  bottom: 60px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #fff;
  text-transform: uppercase;
  z-index: 1;
  @include response(1200px) {
    position: relative;
    right: auto;
    bottom: auto;
    display: inline-flex;
    margin-bottom: 40px;
  }
  @include desktop-hover() {
    .btn-top__icon {
      transform: translateY(-3px);
    }
  }
  &__icon {
    flex: 0 0 40px;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    margin-right: 10px;
    transition: 0.3s ease-in-out;
    svg {
      fill: #fff;
    }
  }
}
