.top-box {
  position: relative;
  background: #f4f4f4;
  padding-top: 40px;
  padding-bottom: 40px;
  @include response(1200px) {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  &__holder {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info {
    position: relative;
    flex: 0 0 auto;
    max-width: 630px;
    width: 100%;
    margin-right: 70px;
  }
  &__img {
    flex: 0 0 auto;
    margin-top: -40px;
    margin-bottom: -40px;
    @include response(1200px) {
      display: none;
    }
  }
  &__title {
    font-family: "Lora";
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    text-align: center;
    color: #0b1014;
    margin: 0 0 20px;
    .top-box__info & {
      text-align: left;
      @include response(1200px) {
        text-align: center;
      }
    }
    @include response(1200px) {
      font-size: 26px;
      line-height: 30px;
      margin-bottom: 10px;
    }
  }
  &__text {
    text-align: center;
    max-width: 720px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    .top-box__info & {
      text-align: left;
      @include response(1200px) {
        text-align: center;
      }
    }
  }
}
