.popup-succes {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
  &.active {
    opacity: 1;
    visibility: visible;
    transform: none;
  }
  &__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#000, 0.5);
  }
  &__box {
    position: relative;
    background: #349724;
    padding: 40px 80px;
    width: 100%;
    @include response(1200px) {
      padding: 30px 50px;
    }
  }
  &__close {
    position: absolute;
    right: 40px;
    top: 50%;
    margin-top: -25px;
    cursor: pointer;
    padding: 10px;
    @include response(1200px) {
      padding: 16px;
      top: 0;
      margin-top: 0;
      right: 0;
    }
    @include desktop-hover() {
      svg {
        transform: rotate(90deg);
      }
    }
    svg {
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        width: 16px;
        height: 16px;
      }
    }
  }
  &__title {
    margin-bottom: 10px;
    font-family: "Lora";
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    text-align: center;
    color: #fff;
    @include response(1200px) {
      font-size: 26px;
      line-height: 30px;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #fff;
  }
}
