.breadcrumb {
  list-style: none;
  padding: 12px 0;
  margin: 0;
  display: flex;
  @include response(1200px) {
    padding: 10px 0;
  }
  li {
    flex: 0 0 auto;
    margin-right: 25px;
  }
  a {
    position: relative;
    display: block;
    text-decoration: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #0b1014;
    @include response(1200px) {
      font-size: 12px;
      line-height: 16px;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
    &::before {
      content: "";
      position: absolute;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: #0b1014;
      top: 50%;
      margin-top: -2px;
      right: -15px;
    }
  }
  span {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #0b1014;
    @include response(1200px) {
      font-size: 12px;
      line-height: 16px;
    }
  }
}
