.articles-list {
  position: relative;
}
.articles-item {
  position: relative;
  display: flex;
  align-items: flex-start;
  margin-bottom: 60px;
  @include response(1200px) {
    margin-bottom: 23px;
  }
  @include response(767px) {
    flex-direction: column-reverse;
  }
  &:last-child {
    margin-bottom: 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &--big {
    flex-direction: column-reverse;
  }
  &__info {
    flex: 1 1 auto;
  }
  &__label {
    margin-bottom: 10px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: #e28b60;
  }
  &__title {
    display: block;
    text-decoration: none;
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
    color: #0b1014;
    margin-bottom: 20px;
    transition: 0.3s ease-in-out;
    max-height: 72px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    @include response(767px) {
      font-size: 26px;
      line-height: 30px;
      max-height: 60px;
      margin-bottom: 8px;
    }
    @include desktop-hover() {
      color: #e28b60;
    }
  }
  &__text {
    margin-bottom: 16px;
  }
  &__footer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__footer-by {
    font-size: 16px;
    line-height: 24px;
    color: #aaa;
    padding-right: 20px;
    @include response(767px) {
      font-size: 12px;
      line-height: 14px;
    }
  }
  &__footer-favorites {
    flex: 0 0 auto;
    display: flex;
    svg {
      fill: #0b1014;
    }
  }
  &__img {
    flex: 0 0 340px;
    max-width: 340px;
    width: 100%;
    background: #c4c4c4;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 30px;
    @include response(767px) {
      margin-left: 0;
      margin-bottom: 16px;
      flex: 0 0 auto;
      max-width: 100%;
    }
    .articles-item--big & {
      flex: 0 0 auto;
      max-width: 100%;
      margin-left: 0;
      margin-bottom: 20px;
      @include response(767px) {
        width: calc(100% + 40px);
        max-width: calc(100% + 40px);
        margin-left: -20px;
        margin-right: -20px;
        border-radius: 0;
      }
    }
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      transition: 0.3s ease-in-out;
    }
  }
}

.articles-load-box {
  position: relative;
}
.articles-btn {
  position: relative;
  @include response(1200px) {
    width: 100%;
  }
}
